import React from "react";

const DisplayVideoList = (props) => {
  return (
    <div className="row-videos">
      <div className="video-list">
        {props.displayList.map((displayListDetails, index) => (
          <div key={index}>
            <div className="video-list-item" key={displayListDetails.videoId}>
              <p>
                <span
                  onClick={() =>
                    props.displayVideo(
                      displayListDetails.videoId,
                      displayListDetails.channelId
                    )
                  }
                >
                  {" "}
                  <b>{displayListDetails.title}</b>
                  <br />
                  {displayListDetails.description}
                  <br />
                  <br />
                  {displayListDetails.publishedAt.substring(0, 10)}
                </span>
                <span
                  className="hide-list"
                  onMouseOver={(e) => {
                    e.target.style.cursor = "pointer";
                  }}
                  onClick={() => {
                    props.hideVideo(
                      displayListDetails.videoId,
                      displayListDetails.channelId
                    );
                  }}
                >
                  Hide
                </span>
              </p>
            </div>
          </div>
        ))}
      </div>
      {props.url ? (
        <div className="video-frame ">
          <iframe
            className="responsive-iframe"
            src={props.url}
            frameBorder="0"
            allow="autoplay; encrypted-media"
            allowFullScreen
            title="video"
          />
        </div>
      ) : null}
    </div>
  );
};

export default DisplayVideoList;
