import React from "react";

const CheckBox = (props) => {
  return (
    <li key={props.channelList.channelId}>
      <input
        onChange={(e) =>
          props.changeChannelList(e, props.channelList.channelId)
        }
        type="checkbox"
        checked={props.channelList.isChecked}
      />{" "}
      {props.channelList.channelName}
    </li>
  );
};

export default CheckBox;
