import React, { useState, useEffect } from "react";
import CheckBox from "./components/Checkbox";
import DisplayVideoList from "./components/DisplayVideoList";
import "./index.css";

const App = () => {
  const [favouriteChannel, setFavouriteChannel] = useState([
    {
      channelId: "UCVTyTA7-g9nopHeHbeuvpRA",
      channelName: "Late Night with Seth Myers",
      isChecked: false,
      maxResult: 10,
    },
    {
      channelId: "UCwWhs_6x42TyRM4Wstoq8HA",
      channelName: "The Daily Show with Trevor Noah",
      isChecked: false,
      maxResult: 10,
    },
    {
      channelId: "UCMtFAi84ehTSYSE9XoHefig",
      channelName: "The Late Show with Stephen Colbert",
      isChecked: false,
      maxResult: 10,
    },
  ]);

  const [videoList, setVideoList] = useState([]);
  const [displayList, setDisplayList] = useState([]);
  const [hiddenList, setHiddenList] = useState([]);

  const [url, setUrl] = useState("");

  //const API_KEY = "AIzaSyDagaG5nAGV-I-I9mTt9-_6wSnGmE-Yhe0";

  //Alternative API_KEY's - in case the quota for above mentioned key gets over
  const API_KEY = "AIzaSyATLijep4MFzYkPAYkQX4HmQXSN4y7_ZzE";
  //const API_KEY = "AIzaSyD-TLUNet49SW2TYEmgKVPNAdOcWQMjrVo";

  const resetVideoList = [];

  useEffect(() => {
    let listToBeDisplayed = videoList
      .filter(
        (hidden) => !hiddenList.find((id) => id.videoId === hidden.videoId)
      )
      .sort((a, b) => new Date(b.publishedAt) - new Date(a.publishedAt))
      .slice(0, 10);

    setDisplayList(listToBeDisplayed);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [videoList]);

  //Function for list updations on deletion of Video list
  const hideVideo = (videoId, channelId) => {
    const newListToBeDisplayed = displayList.filter(
      (videos) => videos.videoId !== videoId
    );

    const hiddenVideos = displayList.filter(
      (videos) => videos.videoId === videoId
    );

    setDisplayList(newListToBeDisplayed);

    hiddenVideos.map((detailsHiddenVideos) =>
      setHiddenList((prevList) => [
        ...prevList,
        { videoId: detailsHiddenVideos.videoId },
      ])
    );

    setFavouriteChannel(
      favouriteChannel.map((channelData) => {
        if (channelId === channelData.channelId) {
          channelData.maxResult = channelData.maxResult + 1;
        }
        return channelData;
      })
    );
  };

  //Function to display videos on click of video list item
  const displayVideo = (videoId, channelId) => {
    setUrl(`https://www.youtube.com/embed/${videoId}`);
    hideVideo(videoId, channelId);
  };

  //Function to update favourite channel list on click of checkbox
  const changeChannelList = (event, channelId) => {
    setFavouriteChannel(
      favouriteChannel.map((channelData) => {
        if (channelId === channelData.channelId) {
          channelData.isChecked = event.target.checked;
        }
        return channelData;
      })
    );
  };

  //Function to fetch video listing asynchronously in parallel and
  //updating the video list
  const searchVideos = async (channels) => {
    try {
      channels.map((channel) =>
        fetch(
          `https://youtube.googleapis.com/youtube/v3/search?part=snippet&channelId=${channel.channelId}&maxResults=${channel.maxResult}&order=date&type=video&key=${API_KEY}`
        )
          .then((res) =>
            res.json().then((responseListItem) =>
              responseListItem.items.map((videoDetails) =>
                setVideoList((prevList) => [
                  ...prevList,
                  {
                    title: videoDetails.snippet.title,
                    description: videoDetails.snippet.description,
                    publishedAt: videoDetails.snippet.publishedAt,
                    videoId: videoDetails.id.videoId,
                    channelId: videoDetails.snippet.channelId,
                  },
                ])
              )
            )
          )
          .catch((error) => {
            document.getElementById("errorDiv").style.display = "block";
            document.getElementById("errorDiv").innerHTML =
              "Your Daily Quota for Fetching API Data is Over. Kindly Try Again Tomorrow";
          })
      );
    } catch (error) {
      document.getElementById("errorDiv").style.display = "block";
      document.getElementById("errorDiv").innerHTML =
        "Your Daily Quota for Fetching API Data is Over. Kindly Try Again Tomorrow";
    }
  };

  //Function to reset url and video list and call search function with desired channel list
  const searchConditions = () => {
    let selectedChannels = favouriteChannel.filter(
      (shows) => shows.isChecked === true
    );

    setUrl("");

    setVideoList(resetVideoList);

    if (selectedChannels.length === 0) {
      searchVideos(favouriteChannel);
    } else {
      searchVideos(selectedChannels);
    }
  };
  return (
    <div className="App">
      <header className="App-header">
        <fieldset>
          <legend>Select your favourite channel</legend>
          <ul className="channel-list">
            {favouriteChannel.map((channelList, index) => (
              <CheckBox
                key={index}
                channelList={channelList}
                changeChannelList={changeChannelList}
              />
            ))}
          </ul>
          <div className="searchButton">
            <input
              type="button"
              value="Search Videos"
              onClick={searchConditions}
            />
          </div>
        </fieldset>
      </header>
      <div id="errorDiv"></div>

      {displayList ? (
        <DisplayVideoList
          displayList={displayList}
          hideVideo={hideVideo}
          displayVideo={displayVideo}
          url={url}
        />
      ) : null}
    </div>
  );
};

export default App;
